.front-face,
.back-face {
  width: 100%;
  height: 100%;
  padding: 20px;
  position: absolute;
  border-radius: 5px;
  background: #ffe100;
  backface-visibility: hidden;
}

.front-face {
  transform: rotateY(180deg);
}

